* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background-color: #e5e5e5;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
  font-size: 14px; }

ul li {
  list-style: none; }

button {
  outline: none;
  border: none; }

button:active, button:focus {
  outline: none; }

.wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column; }

.container {
  width: 320px;
  margin: 0 auto;
  padding: 0 10px; }
  @media (min-width: 768px) {
    .container {
      width: 768px; } }
  @media (min-width: 1280px) {
    .container {
      width: 1120px; } }
